<template>
  <div>
    <h1>checking data</h1>
    <div v-if="!access">
      <h2>Введите код</h2>
      <input type="password" v-model="pass" @keydown.enter="checkPass" />
      <button @click="checkPass">Проверить код</button>
      <p v-if="errorStatus">Код неверен</p>
    </div>
    <div v-if="access" class="project">
      <div class="project-main-frame">
        <input
          type="text"
          placeholder="Введите имя"
          v-model="queryName"
          onkeypress="return /[а-я]/i.test(event.key)"
        />
        <div class="project-main-frame__objects">
          <div v-for="(item, index) in items" :key="index" class="item">
            <span>{{ item.value }}</span>
            <button @click="selectItem(item)">Добавить</button>
          </div>
        </div>
        <p
          class="stop-search"
          v-if="searchState"
          @click="searchState = !searchState"
        >
          Остановить автопоиск
        </p>
        <p class="stop-search" v-else @click="searchState = !searchState">
          Возобновить автопоиск
        </p>
      </div>
      <div class="selected-items">
        <h3>Выбранные предметы</h3>
        <p v-for="(item, index) in selected" :key="index">{{ item.value }}</p>
      </div>
    </div>
  </div>
</template>

<script>
const getData = async function (queryName) {
  const url = 'https://suggestions.dadata.ru/suggestions/api/4_1/rs/suggest/fio';
  const token = '43b7e62f8b5e35438a657ce267979f961dcb9670';
  const query = queryName;

  const options = {
    method: 'POST',
    mode: 'cors',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
      Authorization: `Token ${token}`,
    },
    body: JSON.stringify({ query }),
  };

  return fetch(url, options)
    .then((response) => response.text())
    .then((result) => JSON.parse(result).suggestions)
    .catch((error) => console.log('error', error));
};

export default {
  data() {
    return {
      queryName: '',
      items: [],
      selected: [],
      searchState: true,
      access: false,
      pass: null,
      errorStatus: false,
    };
  },
  watch: {
    async queryName(newVal) {
      if (newVal.length >= 2 && this.searchState) {
        this.items = await getData(this.queryName);
      }
      if (newVal.length === 0) {
        this.items = [];
      }
    },
    async searchState(newVal) {
      if (newVal === true && this.queryName.length >= 2) {
        this.items = await getData(this.queryName);
      }
    },
  },
  methods: {
    selectItem(item) {
      this.selected.push(item);
      console.log(this.selected);
    },
    checkPass() {
      if (this.pass === '019283') {
        this.access = true;
      }
    },
  },
};
</script>

