<template>
  <div class="about">
    <template v-if="!activeTab">
      <h1>Выберите интересующее вас тестовое задание</h1>
      <ul class="about-navigation">
        <router-link custom to="/about/dataCheck" v-slot="{ navigate }">
          <li @click="navigate">data check</li>
        </router-link>
        <router-link custom to="/about/phoneBook" v-slot="{ navigate }">
          <li @click="navigate">phone book</li>
        </router-link>
      </ul>
    </template>
    <DataCheck v-if="activeTab === 'dataCheck'" />
  </div>
</template>

<script>
import DataCheck from '@/components/DataCheck.vue';

export default {
  created() {
    this.$watch(
      () => this.$route.params,
      (toParams) => {
        if (toParams) {
          this.activeTab = toParams.prjName;
        }
      },
    );
  },
  mounted() {
    if (this.$route.params) {
      this.activeTab = this.$route.params.prjName;
    }
  },
  components: {
    DataCheck,
  },
  data() {
    return {
      activeTab: null,
    };
  },
  methods: {
    navigate() {
      this.$route.push('#dataCheck');
    },
  },
};
</script>

